  background: linear-gradient(to right bottom, #ffffff00 49%, #fff 50%), linear-gradient(-50deg, #ffffff 16px, #fffcfc00 0);
  top: -30px;
  -webkit-transform: translateY(-48%);
          transform: translateY(-48%);
}
