/* star rating */
.rating-aside { 
    border: none;
    float: left;
  }
  
  .rating-aside > input { display: none; } 
  .rating-aside > label:before { 
    margin: 5px;
    font-size:2.1em;
    font-family: FontAwesome;
    display: inline-block;
    content: "\F005";
  }
  
  .rating-aside > .half:before { 
    content: "\F089";
    position: absolute;
  }
  
  .rating-aside > label { 
    color: #ddd; 
   float: right; 
  }
  
  .sports-title{
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap
  }
  .sports-title h2{
    font-weight: 600;
