      text-align: right;
  }
  /***** CSS Magic to Highlight Stars on Hover *****/
  
  .rating-aside > input:checked ~ label, /* show gold star when clicked */
  .rating-aside:not(:checked) > label:hover, /* hover current star */
  .rating-aside:not(:checked) > label:hover ~ label { color: #FC9B04;  } /* hover previous stars in list */
  
  .rating-aside > input:checked + label:hover, /* hover current star when changing rating */
  .rating-aside > input:checked ~ label:hover,
  .rating-aside > label:hover ~ input:checked ~ label, /* lighten current selection */
  .rating-aside > input:checked ~ label:hover ~ label { color: #FC9B04;  } 

